function convertToVND(number) {
    if (currencyType === 'USD') {
        const options = {
            style: 'currency',
            currency: 'USD',
            minimumFractionDigits: 0,
            maximumFractionDigits: 2
        };

        let formattedNumber = new Intl.NumberFormat('en-US', options).format(number);
        // Move the currency symbol to the end with a single space
        formattedNumber = formattedNumber.replace('$', '').trim() + ' $';

        return formattedNumber;
    } else if (currencyType === 'VND') {
        return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND', minimumFractionDigits: 0, maximumFractionDigits: 0 }).format(Math.ceil(number));
    }
}
