const observe = new MutationObserver((mutations) => {
    mutations.forEach((mutation) => {
        try {
            if (mutation.type === "childList") {
                const url = document.location.href
                if (url.includes("campaigns?")) {
                    handleReplaceContent(campaignsOption)
                } else if (url.includes("adsets?")) {
                    handleReplaceContent(adsetsOption)
                } else if (url.includes("ads?")) {
                    handleReplaceContent(adsOption)
                }
            }
        } catch (error) {
            console.log("error", error)
        }
    })
})

const intervalId = setInterval(() => {
    if (table) {
        clearInterval(intervalId)
        try {
            const url = document.location.href
            if (url.includes("campaigns?")) {
                handleReplaceContent(campaignsOption)
            } else if (url.includes("adsets?")) {
                handleReplaceContent(adsetsOption)
            } else if (url.includes("ads?")) {
                handleReplaceContent(adsOption)
            }
        } catch (error) {
            console.log("error", error)
        }
        observe.observe(table.childNodes[2], {
            childList: true,
            subtree: true,
            characterData: false,
            attributes: false,
        });
    } else {
        table = document.querySelector("._3h1i._1mie ._3h1j")
    }
}, 200)


window.navigation.addEventListener("navigate", (event) => {
    const url = document.location.href;
    if (
        url.includes("adsmanager.facebook.com/adsmanager/manage/adsets?") ||
        url.includes("adsmanager.facebook.com/adsmanager/manage/ads?") ||
        url.includes("adsmanager.facebook.com/adsmanager/manage/campaigns?")
    ) {
        if (table && table.offsetParent !== null) {
            if (url.includes("adsmanager.facebook.com/adsmanager/manage/adsets?")) {
                handleReplaceContent(adsetsOption)
            } else if (url.includes("adsmanager.facebook.com/adsmanager/manage/ads?")) {
                handleReplaceContent(adsOption)
            } else if (url.includes("adsmanager.facebook.com/adsmanager/manage/campaigns?")) {
                handleReplaceContent(campaignsOption)
            }
            observe.observe(table.childNodes[2], {
                childList: true,
                subtree: true,
                characterData: false,
                attributes: false,
            });
        }
        const intervalId = setInterval(() => {
            if (table && table.offsetParent !== null) {
                clearInterval(intervalId)
                if (url.includes("adsmanager.facebook.com/adsmanager/manage/adsets?")) {
                    handleReplaceContent(adsetsOption)
                } else if (url.includes("adsmanager.facebook.com/adsmanager/manage/ads?")) {
                    handleReplaceContent(adsOption)
                } else if (url.includes("adsmanager.facebook.com/adsmanager/manage/campaigns?")) {
                    handleReplaceContent(campaignsOption)
                }
                observe.observe(table.childNodes[2], {
                    childList: true,
                    subtree: true,
                    characterData: false,
                    attributes: false,
                });
            } else {
                table = document.querySelector("._3h1i._1mie ._3h1j")
                console.log(table)
            }
        }, 100)
    }
    if (url.includes("/insights?")) {
        let els = document.querySelectorAll(".xt9c220 .x1xqt7ti")
        let resultsElement = els[0]
        let costPerResultElement = els[1]
        let spentElement = els[2]
        handleInsights();

        const interval = setInterval(() => {
            els = document.querySelectorAll(".xt9c220 .x1xqt7ti")
            resultsElement = els[0]
            costPerResultElement = els[1]
            spentElement = els[2]
            if (
                resultsElement &&
                costPerResultElement &&
                spentElement
            ) {
                handleInsights();
                clearInterval(interval);
            }
        }, 200);

        function handleInsights() {
            if (resultsElement && costPerResultElement && spentElement) {
                resultsElement.textContent = campaignsOption[0].results.toLocaleString("vi-VN")
                costPerResultElement.textContent = convertToVND((campaignsOption[0].spent / campaignsOption[0].results).toFixed(0))
                spentElement.textContent = convertToVND(campaignsOption[0].spent)
            }
        }
    }
    console.log("location changed! not in adsmanager");
});